import React from 'react';
import IconCosta from '../../../assets/icon_costa.svg';
import IconGreggs from '../../../assets/icon_greggs.svg';
import IconNero from '../../../assets/icon_nero.svg';
import IconMuffin from '../../../assets/icon_muffin.svg';
import IconEsquire from '../../../assets/icon_esquire.svg';
import IconPret from '../../../assets/icon_pret.svg';
import IconStarbuck from '../../../assets/icon_starbuck.svg';
import BrandContext from '../../../contexts/BrandContext';

const BrandLogo = (props) => {
  let currentBrandLogo = null;
  const { brand } = props;
  switch (props.brand) {
    case ('costa'):
      currentBrandLogo = IconCosta
      break;
    case ('greggs'):
      currentBrandLogo = IconGreggs
      break;
    case ('nero'):
      currentBrandLogo = IconNero
      break;
    case ('muffin'):
      currentBrandLogo = IconMuffin
      break;
    case ('esquire'):
      currentBrandLogo = IconEsquire
      break;
    case ('pret'):
      currentBrandLogo = IconPret
      break;
    case ('starbucks'):
      currentBrandLogo = IconStarbuck
      break;
    default:
      currentBrandLogo = null
      break;
  }
  function styleActiveBrand(e) {
    document.querySelectorAll('.TnC-map__brand-logo').forEach(el => {
      el.classList.remove('active-brand')
    })
    e.target.classList.add('active-brand')
  }
  let _class = props.brand === 'costa' ? "TnC-map__brand-logo active-brand" : "TnC-map__brand-logo";
  return (
    <BrandContext.Consumer>{
      context => {
        return <img className={_class}
          src={currentBrandLogo}
          alt={`${currentBrandLogo} icon`}
          onClick={
            () => {
              // eslint-disable-next-line no-restricted-globals
              styleActiveBrand(event);
              context.setBrand(brand);
            }
          } />
      }
    }
    </BrandContext.Consumer>
  )
}
export default BrandLogo
