import React from 'react'
import BrandLogo from './BrandLogo';

const brands = ['costa', 'greggs', 'nero', 'muffin', 'esquire', 'pret', 'starbucks']

function BrandLogos() {
  return (
    brands.map(brand => <BrandLogo key={brand} brand={brand}></BrandLogo>)
  )
}

export default BrandLogos
