import React, { useRef, useState } from 'react'
import "./dropdown.scss"
import MapContext from '../../contexts/MapContext';
import PlaceContext from '../../contexts/PlaceContext';

function DropdownItem(props) {
  function updateLocation() {
    props.setLocation && props.setLocation(props.location);
  }
  return (
    <MapContext.Consumer>
      {
        mapContextData =>
          <span
            className="custom-option"
            data-value="tesla"
            onClick={updateLocation}
          >{props.label} </span>
      }
    </MapContext.Consumer>
  )
}

function Dropdown() {
  const selectWrapper = useRef()
  const [open, setOpen] = useState(false)

  return (
    <div ref={selectWrapper} className="custom-select-wrapper" onClick={
      () => {
        setOpen(!open)
      }
    }>
      <MapContext.Consumer>
        {mapContextData =>
          <PlaceContext.Consumer>
            {placeContextData =>
              <div className="custom-select">
                <h3 className="dropdown_label">Select a Location: </h3>
                <div tabIndex="0" className="custom-select__trigger">
                  <span>{mapContextData && mapContextData.csvData && placeContextData && placeContextData["place"] ? mapContextData.csvData[placeContextData["place"]]['location'] : "Select a location"}</span>
                  <div className="arrow"></div>
                  {/* {mapContextData.csvData && console.log(Object.values(mapContextData.csvData))} */}
                </div>
                {open ?
                  < div className="custom-options">
                    {
                      mapContextData.csvData && (Array.from(Object.values(mapContextData.csvData))
                        .sort((a, b) => a["location"].localeCompare(b["location"]))).map(d => {
                          return <DropdownItem key={d.uid} label={d.location} location={d.uid} setLocation={placeContextData.setPlace}></DropdownItem>
                        })}
                  </div>
                  : ""
                }
              </div>
            }
          </PlaceContext.Consumer>
        }
      </MapContext.Consumer>
    </div >
  )
}
export default Dropdown
