import React, { useRef, useEffect, } from 'react';
import { select, geoPath, geoAlbers, scaleLinear, scaleLog, zoom, event as d3Event } from 'd3'
import BrandContext from '../../contexts/BrandContext';


const ChartWrapper = (props) => {
	const svgRef = useRef();
	const legendRef = useRef();
	const brandLabels = {
		costa: "Costa",
		nero: "Cafe Nero",
		greggs: "Greggs",
		muffin: "Muffin Break",
		pret: "Pret",
		starbucks: "Starbucks",
		esquire: "Esquire",

	}

	// const [chartData, setChartData] = useState()
	// const chartArea = useRef(null)
	// const [chart, setChart] = useState(null)
	function getColour(brand) {
		switch (brand) {
			case ('costa'):
				return "#902833"
			case ('greggs'):
				return "#007BBA"
			case ('nero'):
				return "#668DD6"
			case ('muffin'):
				return "#491225"
			case ('pret'):
				return "#B20D3B"
			case ('esquire'):
				return "#d1a06f"
			case ('starbucks'):
				return "#006241"
			default:
				return null;
		}
	}
	useEffect(() => {
		const svg = select(svgRef.current);
		const legend = select(legendRef.current);
		let HEIGHT = svgRef.current.clientHeight;
		let WIDTH = svgRef.current.clientWidth;


		legend
			.attr("height", HEIGHT)
			.attr("width", "60")
		let projection = geoAlbers()
			.center([1.5, 55.2])
			.rotate([4.4, 0])
			.parallels([50, 60])
			.scale(2600)
			.translate([WIDTH / 2, HEIGHT / 2]);

		//  projection = geoMercator()
		// 	.center([7, 57.2])
		// 	// .rotate([-180, 0])
		// 	// .parallels([50, 60])
		// 	.scale(1400)
		// // .translate([WIDTH / 2, 0]);



		var path = geoPath().projection(projection);
		if (!props.chartData) return

		const csvValues = Array.from(Object.values(props.csvData));
		const maxValue = Math.max(...Array.from(csvValues.map(d => +d['per100k'][props.brand]))) * 100000
		props.setMaxvalue(maxValue)

		let colourScale = scaleLinear().domain([0, maxValue]).range(["#C4C4C4", getColour(props.brand)])
		let colourScaleAlt = scaleLinear().domain([0, maxValue]).range([getColour(props.brand), "#C4C4C4"])
		var def = svg.selectAll('defs').data([null]).join(enter => enter.append('defs'))
		var linearGradient = def.selectAll("linearGradient").data([null])
			.join(enter => enter.append('linearGradient')
				.attr('gradientTransform', 'rotate(90)')
				.attr('id', 'linear-gradient'),
				update => update
					.attr('id', 'linear-gradient')
			)

		linearGradient.selectAll("stop")
			.data([0, maxValue])
			.join(enter => {
				enter.append("stop")
					.attr("offset", d => `${d}%`)
					.attr("stop-color", d => colourScaleAlt(d));
			},
				update => {
					update
						.attr("offset", d => `${d}%`)
						.attr("stop-color", d => colourScaleAlt(d));
				}
			)

		var mapContainer = svg.selectAll('.TnC__map-container').data([null])
			.join(enter =>
				enter.append("g").attr('class', 'TnC__map-container'),
				update => update.attr('class', 'TnC__map-container updated')
			)

		svg.call(zoom().scaleExtent([1, 20]).on('zoom',
			() => {
				mapContainer.attr("transform", d3Event.transform)
				map.selectAll("path").style("stroke-width", 1 / +(d3Event.transform.k))
			}
		))

		function zoomed() {
			mapContainer.attr("transform", d3Event.transform);
		}

		let _zoom = zoom().on("zoom", zoomed)

		select("#TnC__zoom_in").on("click", function () {
			_zoom.scaleBy(svg.transition().duration(250), 1.2);
		});
		select("#TnC__zoom_out").on("click", function () {
			_zoom.scaleBy(svg.transition().duration(250), 0.8);
		});

		legend.selectAll('rect').data([null])
			.join(enter => {
				enter.append('rect')
					.attr("id", "TnC-map__legend")
					.attr("height", "33em")
					.attr("width", "1.5em")
					.attr('rx', "0.8em")
					.style("fill", "url(#linear-gradient)")
					.attr("stroke", "grey")
					.attr("stroke-size", "2")
					.attr("transform", "translate(22, 50)")
			},
				update => update
					.transition().duration(500)
					.style("fill", "url(#linear-gradient)")
			)


		var map = mapContainer.selectAll('.TnC__map').data([null])
			.join(enter =>
				enter.append("g").attr('class', 'TnC__map'),
				update => update.attr('class', 'TnC__map updated')
			)

		map.selectAll('path')
			.data(props.chartData)
			.join(
				enter => {
					enter.append('path')
						.attr("class", "TnC-map__map-path")
						.attr('d', path)
						.attr('fill', (d) => {
							if (d && d['per100k']) return colourScale(+d['per100k'][props.brand] * 100000)
							return "#fff"
						})
						.attr("stroke", '#4d4d4d')
						.attr("stroke-width", '0.05em')
						.attr("cursor", "pointer")
						.on('click', d => { props.setPlace(d.uid) })
						.append('title')
						.text(d => d['location'])

				},
				update => {
					update.attr("class", "TnC-map__map-path")
						// .transition().duration(500)
						.attr('fill', (d) => {
							if (d && d['per100k']) return colourScale(+d['per100k'][props.brand] * 100000)
							return "#fff"
						})
				},
				exit => exit.remove()
			)
	})


	return (
		<BrandContext.Consumer>
			{context => <div className="TnC-map__chart-area">
				<div className="TnC-map__legend-container">
					<svg ref={legendRef} className="TnC-map__legend">
						<text className='TnC-map__legend-text' x="30" y="0.9em" >{brandLabels[context.brand]}</text>
						<text className='TnC-map__legend-text' x="30" y="2.2em" >per 100,000</text>
						<text className='TnC-map__legend-text' x="30" y="3.3em" >people</text>
						<text className='TnC-map__legend-text TnC-map__legend-text--lower' x="40" y="380" >0</text>
						<text className='TnC-map__legend-text TnC-map__legend-text--upper' x="40" y="58" >{props.maxValue ? (props.maxValue / 100000).toFixed(0) : ""}</text>
					</svg>
				</div>
				<div className="TnC__svgContainer">
					<div className="TnC_buttonContainer">

						<button className="TnC__zoom-button TnC__zoom-button--in" id="TnC__zoom_in">+</button>
						<button className="TnC__zoom-button TnC__zoom-button--out" id="TnC__zoom_out">-</button>
					</div>
					<svg ref={svgRef} className="TnC-map__svg"></svg>

				</div>
			</div>}
		</BrandContext.Consumer>
	)
}

export default ChartWrapper