import React, { useEffect, useState } from 'react'
import ChartWrapper from './ChartWrapper'
import BrandLogos from './brands/BrandLogos'
import MapContext from '../../contexts/MapContext';
import BrandContext from '../../contexts/BrandContext';
// import * as topojson from 'topojson-client'

function MapSection() {
  const [maxValue, setMaxvalue] = useState();

  const brandLabels = {
    costa: "Costa",
    nero: "Cafe Nero",
    greggs: "Greggs",
    muffin: "Muffin Break",
    pret: "Pret",
    starbucks: "Starbucks",
    esquire: "Esquire",
  }

  function getColour(brand) {
    switch (brand) {
      case ('costa'):
        return "#902833"
      case ('greggs'):
        return "#007BBA"
      case ('nero'):
        return "#668DD6"
      case ('muffin'):
        return "#491225"
      case ('pret'):
        return "#B20D3B"
      case ('esquire'):
        return "#d1a06f"
      case ('starbucks'):
        return "#006241"
      default:
        return null;
    }
  }
  useEffect(() => {
  }, [])
  return (
    <main className="TnC-map">
      <div className="TnC-map__wrapper">
        <div className="TnC-map__map">
          <h2 className="TnC-map__heading">The density of COFFEE SHOPS per city, <br></br>borough and district across the UK</h2>
          <h3 className="TnC-map__subheading">
            (The deeper the colour = the higher the per capita density of selected coffee shop brand)</h3>
          <MapContext.Consumer>
            {mapContextData =>
              <BrandContext.Consumer>
                {brandContextData => <React.Fragment>
                  <div className="legend2">
                    <div className="legend2__bar" style={{
                      background: `-moz-linear-gradient(left, ${getColour(mapContextData.brand)}, #c4c4c4)`,
                      background: `-webkit-linear-gradient(left, ${getColour(mapContextData.brand)}, #c4c4c4)`,
                      background: `linear-gradient(to right, ${getColour(mapContextData.brand)}, #c4c4c4)`,
                    }}></div>
                    <div className="legend2__text-box">
                      <span>{maxValue && maxValue / 100000}</span>
                      <span>{brandLabels[mapContextData.brand]} per 100,000 people</span>
                      <span>0</span>
                    </div>
                  </div>
                  <ChartWrapper
                    chartData={mapContextData && mapContextData.chartData && mapContextData.chartData}
                    csvData={mapContextData && mapContextData.csvData}
                    brand={brandContextData.brand}
                    setBrand={brandContextData.setBrand}
                    setPlace={mapContextData.setPlace}
                    maxValue={maxValue}
                    setMaxvalue={setMaxvalue}
                  >
                  </ChartWrapper>
                </React.Fragment>
                }
              </BrandContext.Consumer>
            }
          </MapContext.Consumer>
        </div>
        <div>

          <h4 className="TnC-aside__selected-place--attr">Select a Coffee Shop:</h4>
          <div className="TnC-map__brands">
            <BrandLogos></BrandLogos>
          </div>
        </div>
      </div>
    </main>
  )
}

export default MapSection
