import React, { useState, useEffect } from 'react';
import { csv } from 'd3';
import './App.scss';
import LogoGraphics from './assets/LogoTnC.png';
import Aside from './components/Aside';
import MapSection from './components/map-section/MapSection';
import BrandLogoStatic from './components/map-section/brands/BrandLogoStatic';
import Dropdown from './components/dropdown/Dropdown';
import * as topojson from 'topojson-client'
import coffeeshopData from './assets/data/coffeeshop_index2.csv';
import map_data from './assets/data/Local_Authority_Districts__2016_simple1.topo.json';

import MapContext from './contexts/MapContext';
import BrandContext from './contexts/BrandContext'
import PlaceContext from './contexts/PlaceContext'


function App() {
  const brandLabels = {
    costa: "Costa",
    nero: "Cafe Nero",
    greggs: "Greggs",
    muffin: "Muffin Break",
    pret: "Pret",
    starbucks: "Starbucks",
    esquire: "Esquire",

  }

  const [chartData, setChartData] = useState(null)
  const [coffeeshopCsvObj, setCoffeeshopCsvObj] = useState(null)
  const [brand, setBrand] = useState("costa")
  const [place, setPlace] = useState("")

  useEffect(() => {

    const features = topojson.feature(map_data, "Local_Authority_Districts__D");
    // const features = json(map_data);
    const coffeeshopCsv = csv(coffeeshopData);
    Promise.all([features, coffeeshopCsv]).then(values => {
      const csvObj = {}
      values[1].forEach(d => {
        csvObj[d["Location Code"]] = {
          housePrice: d["House Prices"],
          location: d["Location"],
          uid: d["Location Code"],
          population: d["Population"],
          "per100k": {
            nero: +d["A_CaffeNero"],
            costa: +d["A_Costa"],
            esquire: +d["A_Esquires"],
            greggs: +d["A_Greggs"],
            muffin: +d["A_MuffinBreak"],
            pret: +d["A_PretAManger"],
            starbucks: +d["A_Starbucks"],
          },
          "count": {
            caffeNero: +d["C_CaffeNero"],
            costa: +d["C_Costa"],
            esquires: +d["C_Esquires"],
            greggs: +d["C_Greggs"],
            muffinBreak: +d["C_MuffinBreak"],
            pretAManger: +d["C_PretAManger"],
            starbucks: +d["C_Starbucks"],
          }
        }
      })
      setCoffeeshopCsvObj(csvObj)
      setChartData(values[0].features.map(d => {
        const loc_id = d["properties"]["lad16cd"]
        return d = { ...d, ...csvObj[loc_id] }
      }))
    })
  }, [])
  const _placeholder = ["1st", "2nd", "3rd"].map(d => {
    return <div className="top-three" key={d} >
      <span className="top-three top-three__icon">
        <div className="brand-placeholder">{d}</div>
        {/* <BrandLogoStatic brand={d.brand}></BrandLogoStatic> */}
      </span>
      <div className="top-three top-three__text">
        <h5>{brandLabels[d.brand]}</h5>
        <h6><span>{d.value}</span></h6>
      </div>
    </div>
  })

  return (
    <div className="TnC">
      <Aside class="TnC__aside TnC__aside--left">
        <div className="TnC__HeadingContainer">
          <h1 className="TnC__heading">Coffee Shops Index</h1>
        </div>
        <p className="TnC__paragraph--left1">
          Does the value of your home determine the brand of coffee shops you'll have convenient access to?
        </p>
        <p className="TnC__paragraph--left2">
          Our Coffee Shops Index uncovers correlations between the location and density of 7 of the most popular coffee shops in the UK and average house prices for 392 cities, boroughs and districts.
        </p>
        {/* <div className="TnC__LogoGraphics">
          <img className="TnC__LogoGraphics--left" src={LogoGraphics} alt="Section Graphics" />
        </div> */}
      </Aside>
      <BrandContext.Provider value={{ brand: brand, setBrand: setBrand }}>
        <MapContext.Provider value={{ chartData: chartData, csvData: coffeeshopCsvObj, brand: brand, place: place, setPlace: setPlace }}>
          <MapSection />
          <PlaceContext.Provider value={{ place: place, setPlace: setPlace }}>
            <Aside class="TnC-aside TnC__aside--right">
              {/* <div className="TnC-aside TnC-aside__section-right TnC-aside__section-right--1" >
              </div> */}
              <div className="TnC-aside TnC-aside__section-right TnC-aside__section-right--2" >
                <Dropdown></Dropdown>
                <div className="inside-col">

                  <div>
                    <h3 className="TnC-aside__selected-place--name">{coffeeshopCsvObj && coffeeshopCsvObj[place] ? coffeeshopCsvObj[place].location : ""}</h3>

                    <div className="attr-wrapper">
                      <BrandLogoStatic brand="home"></BrandLogoStatic>
                      <h4 className="TnC-aside__selected-place--attr">Average House Price</h4>
                      <div className="u__container u__container--white">
                        {coffeeshopCsvObj && coffeeshopCsvObj[place] ? coffeeshopCsvObj[place].housePrice.replace(".00", "") : ""}
                      </div>
                    </div>

                    <div className="attr-wrapper">
                      <BrandLogoStatic brand={brand}></BrandLogoStatic>
                      <h4 className="TnC-aside__selected-place--attr">Number of {brand ? brandLabels[brand] : "coffe shops"} per 100,000 people</h4>
                      <div className="u__container u__container--white">
                        {coffeeshopCsvObj && coffeeshopCsvObj[place] ? coffeeshopCsvObj[place]["per100k"][brand] : ""}
                      </div>
                    </div>
                  </div>

                  <div>
                    <h4 className="TnC-aside__selected-place--attr">Most poular Coffee Shops in the area</h4>

                    <div className="u__container u__container--tall">
                      {place ? "" : _placeholder}
                      {coffeeshopCsvObj && coffeeshopCsvObj[place] ? Array.from(Object.keys(coffeeshopCsvObj[place]["per100k"]).map(d => {
                        return { brand: d, value: coffeeshopCsvObj[place]["per100k"][d] }
                      })).sort((a, b) => +b.value - +a.value).slice(0, 3).map(d => {
                        return <div className="top-three" key={`1${d.brand}`} >
                          <span className="top-three top-three__icon">
                            <BrandLogoStatic brand={d.brand}></BrandLogoStatic>
                          </span>
                          <div className="top-three top-three__text">
                            <h5>{brandLabels[d.brand]}</h5>
                            <h6><span>{d.value}</span> per 100,000 people</h6>
                          </div>
                        </div>
                      }) : ""}
                    </div>
                  </div>
                </div>


              </div>
            </Aside>
            <Aside >
              <footer className="TnC__aside TnC__aside--bottom"><div className="TnC__LogoGraphics">
                <img className="TnC__LogoGraphics--bottom" src={LogoGraphics} alt="Section Graphics" />
              </div></footer>
            </Aside>
          </PlaceContext.Provider>
        </MapContext.Provider>
      </BrandContext.Provider>
    </div >
  );
}

export default App;
